:host .divider {
  width: 100%;
  margin-bottom: var(--mdx-divider-spacing-margin-bottom);
  margin-top: var(--mdx-divider-spacing-margin-top);

  &__line {
    border-color: var(--mdx-comp-divider-border-color);
    border-width: var(--mdx-comp-divider-border-width-default);
    border-style: solid;
  }
}
